<template>
  <div id="app">
      <van-nav-bar :border="false" v-show="titleShow" :style="$store.state.currentPage === '/movie' ? 'background:#33363D;' : ''" class="m-header">
          <template #title>
              <!-- <van-icon name="search" size="18" /> -->
              <div v-show="$store.state.currentPage === '/home'">
                <van-search
                  placeholder="请输入搜索关键词"
                  input-align="center"
                />
              </div>
              <!-- <div v-show="$store.state.currentPage === '/home'">3212</div> -->
              <div v-show="$store.state.currentPage === '/cinemalist'">影院</div>
              <div class="movieBtn" v-show="$store.state.currentPage === '/movie' || $store.state.currentPage === '/shangying' ">
                <div :class="$store.state.currentPage === '/movie' ? 'active' : ''" @click="toJijiang">正在热映</div>
                <div :class="$store.state.currentPage === '/shangying' ? 'active' : ''" @click="toReying">即将上映</div>
              </div>
          </template>
          <template #left>
              <div style="display:flex;align-items:center" @click="toCity">
                  {{ LocationCity }}
                  <van-icon name="arrow-down" size="18" />
              </div>
          </template>
          <template #right>
              <van-icon name="search" size="18" v-show="$store.state.currentPage !== '/home'" @click="toSearch" />
              <img class="rightIcon" :src="card" v-show="$store.state.currentPage === '/home'" @click="toQiandao">
          </template>
      </van-nav-bar>
    <baidu-map @ready="city"></baidu-map>

    <transition enter-active-class="animated fadeIn">
      <router-view/>
    </transition>

    <div class="tabBar" v-if="barShow">
      <ul>
          <router-link :class="item.classname" :to="item.link" tag="li" v-for="item in list" :key="item.link">
            <img :src="item.img" alt="">
            <img :src="item.imgOn" alt="">
          </router-link>
      </ul>
    </div>
  
  </div>
</template>
<script>
import home from "@/assets/images/icons/home.png"
import homeOn from "@/assets/images/icons/home-on.png"

import movie from "@/assets/images/icons/movie.png"
import movieOn from "@/assets/images/icons/movie-on.png"

import ticket from "@/assets/images/icons/ticket.png"
import ticketOn from "@/assets/images/icons/ticket.png"

import cinema from "@/assets/images/icons/cinema.png"
import cinemaOn from "@/assets/images/icons/cinema-on.png"

import user from "@/assets/images/icons/user.png"
import userOn from "@/assets/images/icons/user-on.png"


import card from "@/assets/images/home/card.png"
export default {
  data () {
    return {
      show: true,
      card,
      list:[
        {
          link:'/home',
          img:home,
          imgOn:homeOn,
          classname:""
        },
        {
          link:'/movie',
          img:movie,
          imgOn:movieOn,
          classname:""
        },
        {
          link:'/home',
          img:ticket,
          imgOn:ticketOn,
          classname:"special"
        },
        {
          link:'/cinemalist',
          img:cinema,
          imgOn:cinemaOn,
          classname:""
        },
        {
          link:'/user',
          img:user,
          imgOn:userOn,
          classname:""
        },
      ],
      LocationCity:"正在定位"
    }
  },
  methods: {
    city(){    //定义获取城市方法
        const geolocation = new BMap.Geolocation();
        var _this = this
        geolocation.getCurrentPosition(function getinfo(position){
            let city = position.address.city;             //获取城市信息
            let province = position.address.province;    //获取省份信息
            _this.LocationCity = city.replace(/市/g, '')
        }, function(e) {
            _this.LocationCity = "定位失败"
        }, {provider: 'baidu'});    
    },    
    toCity() {
      this.$router.push('/city')
      console.log(this.$router)
    },
    toJijiang() {
      this.$router.push('/movie')
    },
    toReying() {
      this.$router.push('/shangying')
    },
    toSearch() {
      this.$router.push('/sousuo')
    },
    toQiandao() {
      this.$router.push('/qiandao')
    }
  },
  mounted() {
  },
  created() {
    // 控制title
    this.$store.state.currentPage = this.$router.currentRoute.fullPath
  },
  computed: {
    titleShow() {
      const currentPage = this.$store.state.currentPage
      return currentPage === '/movie' || currentPage === '/home' || currentPage === '/cinemalist' || currentPage === '/shangying' 
    },
    barShow() {
      const currentPage = this.$store.state.currentPage
      return currentPage === '/movie' || currentPage === '/home' || currentPage === '/cinemalist' || currentPage === '/shangying' || currentPage === '/user'
      // return currentPage !== '/cinema/detail' && currentPage !== '/cinema/map' && currentPage !== '/cinema/cticket' && currentPage !== '/cinema/confirm'
    },
    isHome() {
      return this.$store.state.currentPage === '/home'
    }
  }
}
</script>
<style lang="scss" scoped>
.m-header {
  z-index: 999 !important;
}
#app{
  background-color: #22262D;
  // height: 100vh;
  // background-color: pink;
}
.tabBar{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 236px;
  background: url(~@/assets/images/icons/movie-bg.png) no-repeat center;
  background-size: 100% 100%;
  z-index: 9;
  ul{
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
    li{
      margin-top: 75px;
      img{
        width: 64px;
      
      }
      img:nth-child(2){
        display: none;
      }
    }
    .router-link-active{
      img{
        display: none;
      }
      img:nth-child(2){
        display: block;
      }
    }
    .special{
      img{
        width: 116px;
        margin-top: -100px;
      }
      
    }
  }
}
.van-nav-bar {
  margin-bottom: 24px;
  position: fixed;
  width: 100%;
}
.van-search {
  background: #22262D;
}
.van-search__content {
  background: #33363D;
  box-shadow: 0 0 8px 2px rgba(0,0,0,0.33);
  border-radius: 26px;
  border-radius: 26px;
  height: 60px;
  .van-cell--borderless {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.van-search__action {
  margin-left: 16px;
  color: #F7F7F7;
  font-size: 32px;
  background: #22262D;
}
/deep/ .van-field__left-icon {
  margin-left: 24px;
}
/deep/ .van-icon.van-icon-search {
  font-size: 37px;
  color: #7A7A7A;
}
/deep/ .van-field__body {
  margin-left: -120px;
  input {
    color: #7A7A7A;
  }
}
/deep/ .van-field__control--center {
  text-align: inherit;
  margin-left: 150px;
}

/deep/ .van-field__control::-webkit-input-placeholder {
  color: #7a7a7a
}
/deep/ .van-nav-bar__title {
  width: 100%;
  margin-right: 120px;
}
.rightIcon {
  width: 37.4px;
  height: 36px;
}
.movieBtn {
  display: flex;
  font-size: 28px;
  color: #969393;
  border-radius: 12px 10px 10px 12px;
  background-image: linear-gradient(150deg, #F25B86 0%, #F1AC5E 100%);
  width: 328px;
  margin-right: 0px;
  overflow: hidden;
  position: relative;
  left: 46px;
  div {
    width: 50%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1px;
    margin-bottom: 1px;
    height: 46px;
    &:last-child {
      border-radius: 0 12px 12px 0;
      font-size: 28px;
      margin-right: 2px;
      background-color: #33363D;
    }
    &:first-child {
      border-radius: 12px 0 0 12px;
      margin-left: 2px;
      box-sizing: border-box;
      background-color: #33363D;
    }
  }
  .active {
    color: #fff;
    background-color: transparent !important;
  }
}
</style>
